import myPicker from "@/components/myPicker.vue";
import myCheckboxGroup from "@/components/myCheckboxGroup.vue";
import uniIcon from "@/components/uni-icons/uni-icons.vue";
const JsyServer = require("services/jsy-server.js");
let _this,_customerId;
export default {
	components:{
		myPicker,
		myCheckboxGroup,
		uniIcon
	},
	data() {
		return {
			months:[
				{id: 1,label:'一月'},
				{id: 2,label:'二月'},
				{id: 3,label:'三月'},
				{id: 4,label:'四月'},
				{id: 5,label:'五月'},
				{id: 6,label:'六月'},
				{id: 7,label:'七月'},
				{id: 8,label:'八月'},
				{id: 9,label:'九月'},
				{id: 10,label:'十月'},
				{id: 11,label:'十一月'},
				{id: 12,label:'十二月'}
			],
			operateCapital:[],
			operateCapitalCode: -1,
			operateWom:[],
			operateWomCode: -1,
			operateCredit:[],
			operateCreditCode: -1,
			operateOperation:[],
			operateOperationCode: -1
		};
	},
	
	onLoad:function(options){
		_this = this;
		_customerId = options.customerId;
		console.log(_customerId)
		this.getOperateCapital()
		this.getOperateWom()
		this.getOperateCredit()
		this.getOperateOperation()
		
	},
	
	methods:{
		getOperateCapital:function(){
			JsyServer.getOperateCapital().then(res => {
			  console.log(res);
			  _this.operateCapital = res.data.data.list
			}).catch(err => {
			  wx.showToast({
			    title: err.data.description,
			    icon: 'none'
			  });
			});
		},
		getOperateWom:function(){
			JsyServer.getOperateWom().then(res => {
			  console.log(res);
			  _this.operateWom = res.data.data.list
			}).catch(err => {
			  wx.showToast({
			    title: err.data.description,
			    icon: 'none'
			  });
			});
		},
		getOperateCredit:function(){
			JsyServer.getOperateCredit().then(res => {
			  console.log(res);
			  _this.operateCredit = res.data.data.list
			}).catch(err => {
			  wx.showToast({
			    title: err.data.description,
			    icon: 'none'
			  });
			});
		},
		getOperateOperation:function(){
			JsyServer.getOperateOperation().then(res => {
			  console.log(res);
			  _this.operateOperation = res.data.data.list
			}).catch(err => {
			  wx.showToast({
			    title: err.data.description,
			    icon: 'none'
			  });
			});
		},
		
		tapMonths:function(index){
			let items = this.months
			for (var i = 0, lenI = items.length; i < lenI; ++i) {
				const item = items[i]
				if (item.id == index) {
					this.$set(item,'isChecked',!item.isChecked)
				}
			}
		},
		tapPickerEvent:function(arrName,id){
			switch (arrName){
				case "operateCapital":
                   console.log(id)
				   this.operateCapitalCode = id
				   break;
				case "operateWom":
				  this.operateWomCode = id
				  break;
				case "operateCredit":
				  this.operateCreditCode = id
				  break;
				case "operateOperation":
				  this.operateOperationCode = id
				  break;  
			}
		},
		
		// operateCapitalChange:function(e){
		// 	this.operateCapitalCode =e
		// 	console.log(this.operateCapitalCode)
		// },
		// operateWomChange:function(e){
		// 	this.operateWomCode =e
		// 	console.log(this.operateWomCode)
		// },
		// operateCreditChange:function(e){
		// 	this.operateCreditCode =e
		// 	console.log(this.operateCreditCode)
		// },
		// operateOperationChange:function(e){
		// 	this.operateOperationCode =e
		// 	console.log(this.operateOperationCode)
		// },
		bindCancel:function(){
			uni.navigateBack({
				delta: 1
			});
		},
		submit:function(e){
			
		}
		
	}
}
